.friendList {
  width: 520px;
  margin-left: auto;
  margin-right: auto;
  list-style: none;
}

.item {
  display: flex;
  height: 100px;
  align-items: center;
  box-shadow: 5px 5px 15px 5px rgb(203, 47, 255);
  margin-bottom: 30px;
}

.status {
  display: inline-block;
  margin-right: 10px;
  margin-left: 10px;
  width: 10px;
  height: 10px;
  border-radius: 50%;
}

.online {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  margin-left: 30px;
  margin-right: 50px;
  background-color: green;
}

.ofline {
  width: 20px;
  border-radius: 50%;
  height: 20px;
  background-color: red;
  margin-right: 50px;
  margin-left: 30px;
}

.avatar {
  margin-right: 10px;
}

.name {
  font-weight: 700;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
