.transactionHistory {
  width: 550px;
  margin-top: 40px;
  margin-left: auto;
  margin-right: auto;
  text-align: justify;
}

table {
  border-collapse: initial;
  border-spacing: 1px;
}

thead {
  background-color: rgb(10, 174, 245);
}

td {
  text-align: center;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
th {
  height: 60px;
  text-align: center;
}

tr:nth-child(2n) {
  background-color: palegreen;
}
